.whole-container-two{
    width: 100%;
    min-height: 95vh;
    margin-top: 2%;
    /* border: 1px solid red; */
}
.intro{
    color: rgb(221, 213, 213);
    line-height: 1.6;
    text-align: center;
    font-size: 0.9rem;
text-transform: capitalize;
    }
.sp-2{
   color:  rgb(28, 157, 207);
   font-size: 1.4rem;
}
.about-flex-con{
    width: 80%;
    min-height: 70vh;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    /* border: 1px solid green; */
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.box-img{
    width:35%;
    max-height: 53vh;
    margin-top: 3%;
    background: linear-gradient(45deg,rgb(87, 202, 218),rgb(34, 108, 204));
    border-radius: 1.7rem;
}
.real-img{
width: 100% ;
max-height: 53vh;
transform: rotate(10deg);
border-radius: 1.7rem;  
transition: all 0.8s;
}
.real-img:hover{
    transform: rotate(0deg);
}
.para-container{
    width: 57%;
    min-height: 60vh;
    /* border: 1px solid yellow; */
}
.paper-container{
    width: 100%;
    max-height: 30vh;  
     /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
}
.paper-container .fl-p-1{
    width: 25%;
    padding:6% 0%;
    border-radius: 1rem;
    background-color: rgba(48, 90, 146, 0.5);
    color:rgb(28, 157, 207);
}
.award{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}
.fl-p-1 p{
    font-size: 0.85rem;
    text-align: center;
    color: rgb(211, 206, 206);
    line-height: 1.6;
}
.fl-p-1 p::first-line{
    font-size: 1.3rem;
}
.lorem{
    margin-top: 3%;
    color: rgb(211, 206, 206);
    line-height: 1.6;
    font-size: 0.9rem;
}
.lorem-link{
    width: 15%;
      background-color: #1f1f38;
      padding: 2% ;
      margin-top: 4.5%;
      display: inline-block;
      text-align: center;
      text-decoration: none;
      color: black;
      background-color: rgb(44, 135, 253);
      border-radius: 0.5rem;
      border: 1px solid rgba(101, 166, 240,0.5);
      transition: all 0.8s;
      cursor: pointer;
  }
  .lorem-link:hover{
    border: 1px solid white;
  }
  @media only screen and (max-width:600px){
.about-flex-con{
    flex-direction: column;
}
.box-img{
    width: 80%;
    max-height: 40vh;
}
.real-img{
    max-height: 40vh;
}
.para-container{
    margin-top: 6%;
    width: 100%;
}
.paper-container{
min-height: 75vh;

flex-direction: column;
justify-content: space-around;
}
 .paper-container .fl-p-1{
    width: 100%;
    padding:6% 0% 13%;
 
}
.lorem-link{
    width: 50%;
    font-size: 0.9rem;
}
  }