.whole-container-four{
    width: 100%;
    min-height: 85vh;
    /* border: 1px solid red; */
}
.service-container{
    width: 100%;
    min-height: 70vh;
    /* border: 1px solid yellow; */
    display: flex;
    justify-content: center;
    gap: 2rem;
}
.ui-container{
    width: 20%;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius:2rem ;
    background-color: rgba(48, 90, 146, 0.5);
    border: 1px solid rgba(255, 255, 255,0.3);
    border-top: 1px solid transparent;
    transition: all 0.8s;
}
.ui-container:hover{
    background-color: rgba(44, 135, 253,0.5);
}

.ui-box{
    width: 100%;
    text-align: center;
    padding: 10% 0%;
    font-weight: 500;
    background-color: rgb(44, 135, 253);
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius:2rem ;
    
}
.why{
    padding-left: 5%;
    margin-top: 4%;
    /* border: 1px solid red; */
    color: rgba(255, 255, 255,0.7);
}
.mark{
    padding-left: 2%;
    padding-top: 2.5%;
    color: rgb(28, 157, 207);
    font-size: 1.4rem;
}
.sp-mark{
    padding-left: 3%;
}
.sp-mark-2{
    margin-left: 11%;
}
@media only screen and (max-width:600px){
.service-container{
flex-direction: column;
align-items: center;

}
.ui-container{
    width: 70%;
    padding-bottom: 7%;
}

}