.whole-container-last{
    width: 100%;
    background-color: rgb(44, 135, 253);
    padding: 3% 0% 5% 0%;
    margin-top: 2%;
}
.last-container-footer{
    width: 60%;
    /* border: 1px solid yellow; */
    padding-bottom: 3.5%;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}
.icon-footer-container{
    width:15%;
    display: flex;
    justify-content: space-between;
    /* border: 1px solid red; */
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}
.icon-footer{
    width: 26%;
    padding: 5% 3% 3% 7%;
    border-radius: 0.3rem;
    color: rgba(255, 255, 255,0.8);
    background-color: rgb(30, 37, 80);
    
}
.para-one{
    font-size: 1.8rem;
    text-transform: uppercase;
    font-weight: 700;
    text-align: center;
    line-height: 2.5;
    color: rgb(31, 29, 29);
}
.para-two{
    word-spacing: 1.2rem;
    font-weight: 600;
    text-align: center;
    color: rgb(37, 35, 35);
    line-height: 2.5;
    text-transform: capitalize;
}
.para-three{
    font-weight: 400;
    color: rgb(37, 35, 35);
    text-transform: capitalize; 
    line-height: 4.5;
    text-align: center;
}
@media only screen and (max-width:600px){
.last-container-footer{
    width: 85%;
}
.icon-footer-container{
    width: 55%;
}
.para-three{
    font-size: 0.8rem;
}
}