.whole-container-six{
    width: 100%;
    min-height: 75vh;
    /* border: 1px solid red; */
    margin-top: 2%;
}
.swiper-whole{
    width: 50%;
    height: 55vh;
    /* border: 1px solid purple; */
    background-color: #1f1f38;
}
.test-circle{
    width: 100%;
    margin-top: 2%;
    background-color: rgba(48, 90, 146, 0.5);
    border-top-right-radius: 1.5rem;
    border-bottom-right-radius: 1.5rem;
    padding:2% 3% 6% 2%  ;
  
}
.img-circle{
    width: 15%;
    border-radius: 50%;
    min-height: 15vh;
    object-fit: cover;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    border: 10px solid rgba(48, 90, 146, 0.8);;
}
.p-header{
    color: rgba(255, 255, 255,1);
    text-transform: capitalize;
    font-size: 1.4rem;
    text-align: center;
}
.lorem-2{
    color: rgba(255, 255, 255,0.6);
    text-align: center;
    line-height: 1.5;
}
@media only screen and (max-width:500px){
    .swiper-whole{
        width: 90%;
        min-height: 67vh;
    }
    .test-circle{
        font-size: 0.9rem;
    }
    .img-circle{
        width: 34%;
    }
}