.whole-container-three{
    width: 100%;
    min-height: 60vh;
    margin-top: 3%;
    /* border: 1px solid red; */
}
.ex-flex-con{
    display: flex;
    margin-top: 2%;
    /* border: 1px solid yellow; */
    justify-content: space-around;
}
.ex-con-1{
    width: 40%;
    border-radius: 2rem;
    background-color: rgba(48, 90, 146, 0.5);
}
.ex-con-2{
    width: 40%;
    min-height: 45vh;
    border-radius: 2rem;
    background-color: rgba(48, 90, 146, 0.5);
}
.f-head{
text-align: center;
padding-top: 3%;
color: rgb(28, 157, 207);
}
.ul-con{
    display: flex;
    width: 100%;
    margin: 4%;
    color: rgba(255, 255, 255,0.7);
    justify-content: space-around;
}
.f-ul , .s-ul{
    display: flex;
    height: 30vh;
    flex-direction: column;
    justify-content: space-between;
   
}
.f-ul li ,.s-ul{
   list-style-type: none;
}
.bs{
    color: rgb(28, 157, 207);;
}
@media only screen and (max-width:600px){
.ex-flex-con{
    flex-direction: column;
    min-height: 100vh;
    align-items: center;

}
.ex-con-1,.ex-con-2{
    width: 90%;
}
}
