.whole-container-five{
    width: 100%;
    min-height: 85vh;
    /* border: 1px solid red; */
    margin-top: 2%;
}
.port-flex-1{
    width: 70%;
    /* border: 1px solid yellow; */
    display: flex;
    justify-content: space-between;
    position: relative;
    align-items: center;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 2%;
}
.port-flex-2{
    width: 70%;
    /* border: 1px solid yellow; */
    display: flex;
    align-items: center;
    justify-content: space-between;
  
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 2%;
}
.github-box{
    width:28%;
    padding:2% 0% 5% 2%;
    margin-top: 3%;
    border-radius: 1.5rem;
    background-color: rgba(48, 90, 146, 0.5);
}
.github-box img{
    width: 92%;
    border-radius: 1.5rem;
}
 .let{
    color: rgba(255, 255, 255, 0.747);
    margin-top: 4%;
    text-transform: capitalize;
}
.demark{
    display: flex;
    width: 90%;
    justify-content: space-around;
    /* border: 1px solid red; */
    margin-top: 4%;
}
.cv-one-gt{
    width:40%;
    font-size:0.95rem;
    background-color: #1f1f38;
    padding: 4.9% ;
    display: inline-block;
    text-decoration: none;
    text-align: center;
    color: rgba(101, 166, 240,0.5);
    border-radius: 0.5rem;
    border: 1px solid rgba(101, 166, 240,0.5);
    transition: all 0.8s;
    cursor: pointer;
}
.cv-one-gt:hover ,.cv-two-gt:hover{
    background-color: rgba(42, 208, 230,0.5);
    color: black;
}
.cv-two-gt{
  width: 46%;
  font-size:0.95rem;
    background-color: #1f1f38;
    padding: 4.9% ;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    color: rgba(101, 166, 240,0.5);
    border-radius: 0.5rem;
    border: 1px solid rgba(101, 166, 240,0.5);
    transition: all 0.8s;
    cursor: pointer;
}
@media only screen and (max-width:600px){
.port-flex-1,.port-flex-2{
    flex-direction: column;
    width: 80%;
    justify-content: space-around;
}
.github-box{
    width: 90%;
}
.cv-two-gt{
    font-size: 0.9rem;
}
}