.whole-container-seven{
    width: 100%;
    min-height: 97vh;
    /* border: 1px solid red; */
}
.social-container{
    width: 70%;
    min-height: 83vh;
    /* border: 1px solid yellow; */
    display: flex;
    margin-top: 1.5%;
    justify-content: space-around;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}
.social-paper-container-1{
    width:25%;
    min-height:80vh;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.social-paper-container-2{
    width:50%;
    min-height:80vh;
    /* border: 4px solid purple; */
}
.social-paper{
    width: 100%;
    background-color: rgba(48, 90, 146, 0.5);
    padding: 12% 0% 12% 0%;
    border-radius: 1rem ;
}
.text-p{
    line-height: 1.5;
}
.email{
    position: relative;
    left: 46%;
    transform: rotateX(-50%);
    font-size: 1.5rem;
    color: rgba(255, 255, 255,0.5);
}
.text-p{
    text-align: center;
    color: white;
}
.send{
    color: rgb(47, 155, 228);
    text-decoration: none;
}
.send:hover{
    text-decoration: underline;
}
[type="text"]{
    display: block;
    width: 83%;
    padding-left: 4%;
    padding-top: 2.5%;
    padding-bottom: 2.5%;
    color: rgba(255, 252, 252,0.8);
    background-color: #1f1f38;
    border-radius: 0.6rem;
    border: 0.8px solid rgba(255, 252, 252,0.5);
}
[type="email"]{
    display: block;
    width: 83%;
    padding-left: 4%;
    padding-top: 2.5%;
    padding-bottom: 2.5%;
    margin-top: 3%;
    color: rgba(255, 252, 252,0.8);
    background-color: #1f1f38;
    border-radius: 0.6rem;
    border: 0.8px solid rgba(255, 252, 252,0.5);
}
[type="text"]::placeholder,[type="email"]::placeholder,textarea::placeholder{
    color: rgba(255, 252, 252,0.8);
}
textarea{
    background-color: #1f1f38;  
    resize: none;
    padding-left: 4%;
    padding-right: 2%;
    color: rgba(255, 252, 252,0.8);
    padding-top: 2%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    margin-top: 3%;
}
[type="submit"]{
    width: 22%;
    background-color: rgb(44, 135, 253);
    padding: 2% 0% 2% 0%;
    border-radius: 0.5rem;
    margin-top: 2%;
    border: 1px solid rgb(44, 135, 253);
    font-weight: bold;
    display: block;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    cursor: pointer;
}
[type="submit"]:active{
transform: translateY(2px);
}
@media only screen and (max-width:600px){
    .social-container{
        flex-direction: column;
        width: 80%;
    
    }
.social-paper-container-1{
    width: 100%;
    min-height: 83vh;
}
.social-paper-container-2{
    width: 100%;
    min-height: 58vh;
    margin-top: 4%;
}
.social-paper{
    padding-bottom: 9%;
    padding-top: 9%;
}
[type="text"],[type="email"]{
    width: 100%;
}
[type="submit"]{
    width: 37%;
    font-size: 0.8rem;
}
textarea{
width: 100%;
}
}