
.whole-container{
    width: 100%;
    min-height: 95vh;
    /* border: 1px solid red; */
}
.line-con{
color: rgb(247, 241, 241);
line-height: 1.6;
text-align: center;
margin-top: 3%;
}
.sp-1{
text-transform: uppercase;
font-size: 1.4rem;
}
.cv-container{
    display:flex;
    width: 22%;
    justify-content: space-between;
    /* border: 1px solid red; */
    position: relative;
    margin-top: 2%;
    left: 50%;
    transform: translateX(-50%);
}
.cv-one{
    width:50%;
    background-color: #1f1f38;
    padding: 4.9% ;
    display: inline-block;
    text-decoration: none;
    text-align: center;
    color: rgba(101, 166, 240,0.5);
    border-radius: 0.5rem;
    border: 1px solid rgba(101, 166, 240,0.5);
    transition: all 0.8s;
    cursor: pointer;
}
.cv-one:hover ,.cv-two:hover{
    background-color: rgb(42, 208, 230);
    color: black;
}
.cv-two{
  width: 44%;
    background-color: #1f1f38;
    padding: 4.9% ;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    color: rgba(101, 166, 240,0.5);
    border-radius: 0.5rem;
    border: 1px solid rgba(101, 166, 240,0.5);
    transition: all 0.8s;
    cursor: pointer;
}
.sl-container{
    display: flex;
    justify-content: center;
    margin-top: 3%;
    /* border: 1px solid rebeccapurple; */
}
.icon-container{
    width: 5%;
    /* border: 1px solid yellow; */
    display: flex;
    position: relative;
    left: -20%;
    flex-flow: column nowrap;
    justify-content: center;
    gap: 1.5rem;
}
.icon-container a{
    color: rgba(101, 166, 240,0.5);
}
hr{
   border:1px dotted rgba(101, 166, 240,0.5);
   transform: rotate(90deg);
   position: relative;
   top: 8%;
   right:38%
}

.linear{
width: 21%;
background: linear-gradient(to bottom,rgb(87, 202, 218),rgb(34, 108, 204));
padding: 4% 0% 7% 4%;
border-top-right-radius: 43%;
border-top-left-radius: 43%;

}
.linear:hover .bg{
    width:78%;
}
.bg{
  width: 75%;  
  transition: all 1s;
}
.scroll-down{
    color:rgba(101, 166, 240,0.5);
    transform: rotate(90deg);
    text-decoration: none;
    position: relative;
    height: 4vh;
    top: 12rem;
    left: 14rem;
    transition: all 0.5s;
}
.scroll-down:hover{
    transform: rotate(91deg);
}
@media only screen and (max-width:600px){
.whole-container{
    min-height: 80vh;
}
    .cv-container{
    width: 60%;
    font-size: 0.8rem;
}
.scroll-down{
    display: none;
}
.sl-container{
    flex-direction: column;
    align-items: center;
    min-height: 50vh;
    justify-content: space-around
}
.icon-container{
    left: 0%;
}
hr{
    top: 3%;
    right: 20%;
}
.linear{
    width: 70%;
    padding-left: 10%;
}

}