*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.Nav-container{
    width: 100%;
    display: flex;
    justify-content: center;
}
nav{
    width: 22%;
    background-color: rgba(14, 13, 13,0.7);
    padding: 1%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 3rem;
    position: fixed;
    z-index: 4;
    bottom: 0;
    margin-bottom: 1%;
}
nav a{
    color: rgba(255, 255, 255,0.6);
}
.encircle{
    width:13%;
    background-color: rgb(34, 108, 204);
    padding: 2.8% 0% 2.5% 3.5%;
    color: black;
    border-radius: 50%;
}
@media only screen and (max-width:600px){
    nav{
        width: 70%;
        padding: 2.5%;
        margin-bottom: 3%;
    }
    .encircle{
        width: 15%;
    }
}